* {
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 20px;
}

input{
  font-size: 1.2rem;
  padding: 10px;
  width: 100%;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  height: 100%;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width: 31%;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  width: 100%;
}

.container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.container h3{
  width: 100%;
}